.links_item--brands {
  height: 72px;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  margin-left: 16px;
  margin-right: auto;
  margin-bottom: 16px; }
  @media (min-width: 768px) {
    .links_item--brands {
      margin-left: 24px; } }
  @media (min-width: 1120px) {
    .links_item--brands {
      margin-left: 32px; } }
  @media (min-width: 1366px) {
    .links_item--brands {
      margin-left: 19px; } }

.links_item--repair {
  flex-direction: column;
  height: 160px;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  margin-left: 16px;
  margin-right: auto;
  margin-bottom: 16px; }
  @media (min-width: 768px) {
    .links_item--repair {
      margin-left: 24px; } }
  @media (min-width: 1120px) {
    .links_item--repair {
      margin-left: 32px; } }
  @media (min-width: 1366px) {
    .links_item--repair {
      margin-left: 19px; } }

.links_item--side-bar {
  height: 24px;
  margin-bottom: 16px; }

.links_anchor {
  text-decoration: none;
  font-family: TTLakesMedium;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21; }

.links_anchor--active {
  color: #7E7E82; }
  .links_anchor--active::before {
    content: " ";
    position: absolute;
    top: 85px;
    left: 0;
    width: 4px;
    height: 32px;
    background: #41F6D7;
    border-top-right-radius: 45px;
    border-bottom-right-radius: 45px; }

.links_item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 240px;
  /*margin-left: 16px;
    margin-right: auto;
    margin-bottom: 16px;*/
  background-color: #FFFFFF; }
  @media (min-width: 768px) {
    .links_item {
      width: 224px;
      /*margin-left: 24px;*/ } }
  @media (min-width: 1120px) {
    .links_item {
      width: 240px;
      /* margin-left: 32px;*/ } }
  @media (min-width: 1366px) {
    .links_item {
      /*margin-left: 19px;*/ } }
  .links_item .links_item-image {
    margin-left: 16px; }
  .links_item .links_item-text {
    margin-left: 16px;
    margin-top: 16px;
    align-self: flex-start;
    font-family: 'TTLakesMedium';
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21; }
  .links_item .links_item-button {
    margin-left: auto;
    margin-right: 16px;
    margin-bottom: 16px;
    margin-top: auto; }
