@font-face {
  font-family: 'TTLakesRegular';
  src: url(./fonts/TTLakes-Regular.ttf), url(./fonts/TTLakes-Regular.eot) format("eot"), url(./fonts/TTLakes-Regular.woff) format("woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'TTLakesMedium';
  src: url(./fonts/TTLakes-Medium.ttf), url(./fonts/TTLakes-Medium.eot) format("eot"), url(./fonts/TTLakes-Medium.woff) format("woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'TTLakesBold';
  src: url(./fonts/TTLakes-Bold.ttf), url(./fonts/TTLakes-Bold.eot) format("eot"), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 700;
  font-style: normal; }

@keyframes scale-rotate {
  from {
    transform: scale(1.5); }
  50% {
    transform: scale(1.5) rotate(360deg); } }

.side-bar {
  display: none;
  position: fixed;
  z-index: 11;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  left: 0; }
  @media (min-width: 1366px) {
    .side-bar {
      display: block;
      z-index: unset;
      height: unset;
      position: fixed;
      width: 320px;
      padding-bottom: 100%;
      background-color: #FFFFFF; } }
  .side-bar .side-bar_container {
    width: 320px;
    height: 100%;
    box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2);
    background-color: #FFFFFF; }
    @media (min-width: 1366px) {
      .side-bar .side-bar_container {
        box-shadow: none; } }
  .side-bar .side-bar_header {
    display: flex;
    flex-wrap: wrap;
    height: 88px;
    width: 320px;
    align-items: center;
    background-color: #FFFFFF; }
    .side-bar .side-bar_header .side-bar_button {
      margin: 0 16px; }
    .side-bar .side-bar_header .side-bar_logo {
      margin-right: auto; }
      @media (min-width: 1366px) {
        .side-bar .side-bar_header .side-bar_logo {
          margin-left: 16px; } }
  .side-bar .side-bar_links {
    background-color: #FFFFFF;
    margin: 0;
    padding: 0;
    width: 304px; }
    @media (min-width: 1366px) {
      .side-bar .side-bar_links {
        width: unset; } }
  .side-bar .side-bar_footer {
    padding-top: 190px;
    display: flex;
    flex-wrap: wrap;
    width: 320px;
    background-color: #FFFFFF; }
  .side-bar .side-bar_button {
    margin-left: 16px; }
    .side-bar .side-bar_button:hover {
      border-radius: 50%;
      animation-name: scale-rotate;
      animation-duration: 1.5s;
      box-shadow: 0px 0px 5px 5px #7E7E82; }
  .side-bar .side-bar_email {
    text-decoration: none;
    width: 320px;
    height: 25px;
    margin-top: 16px;
    margin-left: 16px;
    font-family: 'TTLakesMedium';
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21; }
  .side-bar .side-bar_tel {
    text-decoration: none;
    width: 320px;
    height: 25px;
    margin-top: 16px;
    margin-left: 16px;
    margin-bottom: 40px;
    font-family: 'TTLakesBold';
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21; }
  .side-bar .side-bar_links {
    display: flex;
    flex-wrap: wrap;
    margin-left: 16px; }

@media (min-width: 1366px) {
  .side-bar_button--close {
    display: none; } }

.show {
  display: block; }
