@font-face {
  font-family: 'TTLakesRegular';
  src: url(./fonts/TTLakes-Regular.ttf), url(./fonts/TTLakes-Regular.eot) format("eot"), url(./fonts/TTLakes-Regular.woff) format("woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'TTLakesMedium';
  src: url(./fonts/TTLakes-Medium.ttf), url(./fonts/TTLakes-Medium.eot) format("eot"), url(./fonts/TTLakes-Medium.woff) format("woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'TTLakesBold';
  src: url(./fonts/TTLakes-Bold.ttf), url(./fonts/TTLakes-Bold.eot) format("eot"), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 700;
  font-style: normal; }

.article {
  display: flex;
  flex-wrap: wrap;
  min-width: 320px; }
  @media (min-width: 705px) {
    .article {
      flex-wrap: nowrap; } }
  .article .article_text {
    display: flex;
    flex-wrap: wrap;
    margin-right: auto;
    flex-direction: column; }
  .article .article_image {
    display: block;
    min-width: 320px;
    height: 176px;
    margin-right: auto;
    margin-left: auto;
    background-image: url(./static/img-phone.jpg); }
    @media (min-width: 705px) {
      .article .article_image {
        margin: 0 24px; } }
    @media (min-width: 768px) {
      .article .article_image {
        display: flex;
        min-width: 360px;
        height: 208px;
        background-image: url(./static/img-tablet.jpg); } }
    @media (min-width: 1120px) {
      .article .article_image {
        min-width: 532px;
        height: 307px;
        background-image: url(./static/img-big.jpg); } }

.text_content {
  margin: 0;
  display: flex;
  height: 90px;
  overflow: hidden;
  font-family: 'TTLakesRegular';
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21;
  padding: 0 16px;
  margin-bottom: 16px; }
  @media (min-width: 705px) {
    .text_content {
      max-width: 417px; } }
  @media (min-width: 768px) {
    .text_content {
      max-width: 957px;
      height: 160px;
      padding: 0 24px; } }

.text_content--show {
  height: unset; }
