  font-weight: 700;
  font-style: normal; }

.button-more {
  font-family: 'TTLakesMedium';
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  /*.button-more_show-more--show {
      margin-left: 24px;
      margin-bottom: 16px;
      @media (min-width: 1120px) {
        margin-left: 32px;
      };
    }*/
  /*.button-more_show-less--show {
      margin-left: 24px;
      margin-bottom: 16px;
      @media (min-width: 1120px) {
        margin-left: 32px;
      };
    };*/ }
  .button-more .button-more_show-more {
    display: none;
    margin-left: 24px;
    margin-bottom: 16px; }
    @media (min-width: 768px) {
      .button-more .button-more_show-more {
        display: block; } }
    @media (min-width: 1120px) {
      .button-more .button-more_show-more {
        margin-left: 32px; } }
    .button-more .button-more_show-more::before {
      display: inline-block;
      content: url(./static/expand.png);
      transform: translateY(20%); }
  .button-more .button-more_show-less {
    margin-left: 24px;
    margin-bottom: 16px; }
    .button-more .button-more_show-less::before {
      display: inline-block;
      content: url(./static/expand.png);
      transform: translateY(15%) rotate(180deg); }
    @media (min-width: 1120px) {
      .button-more .button-more_show-less {
        margin-left: 32px; } }
  .button-more .button-more--hide {
    display: none; }
