.section {
  overflow: hidden;
  margin-top: 16px; }
  .section .section_content {
    display: flex;
    flex-wrap: wrap; }
    .section .section_content .section_links {
      display: flex;
      padding: 0;
      margin-bottom: 0;
      flex-grow: 1; }
      @media (min-width: 768px) {
        .section .section_content .section_links {
          flex-wrap: wrap;
          margin-top: 24px;
          min-width: 768px;
          max-height: 175px;
          overflow: hidden; } }
    .section .section_content .section_links--show {
      max-height: unset; }
