@font-face {
  font-family: 'TTLakesRegular';
  src: url(./fonts/TTLakes-Regular.ttf), url(./fonts/TTLakes-Regular.eot) format("eot"), url(./fonts/TTLakes-Regular.woff) format("woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'TTLakesMedium';
  src: url(./fonts/TTLakes-Medium.ttf), url(./fonts/TTLakes-Medium.eot) format("eot"), url(./fonts/TTLakes-Medium.woff) format("woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'TTLakesBold';
  src: url(./fonts/TTLakes-Bold.ttf), url(./fonts/TTLakes-Bold.eot) format("eot"), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 700;
  font-style: normal; }

.popup--call {
  display: none; }

.popup--feedback {
  display: none; }

.popup {
  position: fixed;
  z-index: 11;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  left: 0; }
  .popup .popup_container {
    position: fixed;
    right: 0;
    height: 100%;
    background-color: #FFFFFF;
    flex-direction: column;
    width: 288px;
    padding: 24px 16px;
    box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2); }
    @media (min-width: 544px) {
      .popup .popup_container {
        width: 376px;
        padding: 40px 32px; } }
  .popup .popup_button--close {
    margin-bottom: 40px; }
    @media (min-width: 544px) {
      .popup .popup_button--close {
        margin-bottom: unset;
        position: absolute;
        top: 35px;
        left: -72px; } }
  .popup .popup_form-field {
    margin-bottom: 16px;
    width: 270px;
    height: 48px;
    background: #FFFFFF;
    border: 1px solid #EAEAEA;
    border-radius: 8px;
    font-family: 'TTLakesRegular';
    font-size: 16px;
    line-height: 24px;
    color: #B5B6BC;
    padding-left: 16px; }
    @media (min-width: 544px) {
      .popup .popup_form-field {
        width: 358px; } }
  .popup .popup_form-field--message {
    height: 106px;
    resize: none; }
  .popup .popup_title {
    margin: 0;
    margin-bottom: 24px;
    position: relative;
    font-family: 'TTLakesBold';
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    color: #1B1C21; }
    .popup .popup_title::before {
      content: " ";
      position: absolute;
      left: -16px;
      top: -4px;
      width: 4px;
      height: 40px;
      background: #41F6D7;
      border-top-right-radius: 45px;
      border-bottom-right-radius: 45px; }
      @media (min-width: 544px) {
        .popup .popup_title::before {
          left: -32px; } }
  .popup .popup_text {
    margin: 0;
    margin-bottom: 18px;
    width: 288px;
    font-family: 'TTLakesRegular';
    font-size: 14px;
    line-height: 16px;
    color: #7E7E82; }
    @media (min-width: 544px) {
      .popup .popup_text {
        width: 376px; } }

.show {
  display: block; }
