.button-order {
  position: relative;
  border: 2px solid #FF3E79;
  border-radius: 50%;
  margin-left: auto;
  margin-right: 16px;
  width: 40px;
  height: 40px; }
  .button-order::after {
    position: absolute;
    top: 13px;
    left: 11px;
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    border-left: 3px solid #FF3E79;
    border-bottom: 3px solid #FF3E79;
    transform: rotate(225deg); }
