.body {
  background-color: #F8F8F8;
  margin: 0;
  padding: 0; }
  .body .header {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    min-width: 320px;
    height: 88px;
    background-color: #FFFFFF; }
    @media (min-width: 1366px) {
      .body .header {
        margin-left: 320px;
        min-width: 1046px; } }
    .body .header .header_tablet {
      display: flex;
      flex-wrap: wrap;
      flex-grow: 1;
      align-items: center;
      padding: 0 16px; }
      @media (min-width: 1366px) {
        .body .header .header_tablet {
          display: none; } }
      .body .header .header_tablet .header_tablet-button {
        margin-right: 16px; }
        .body .header .header_tablet .header_tablet-button:last-child {
          margin-right: 0; }
      .body .header .header_tablet .header_logo-block {
        display: flex;
        flex-wrap: wrap;
        flex-grow: 1;
        align-items: center; }
        .body .header .header_tablet .header_logo-block::before {
          display: none;
          content: '';
          height: 32px;
          width: 2px;
          border-radius: 1px;
          margin-right: 20px;
          background-color: #EAEAEA; }
          @media (min-width: 343px) {
            .body .header .header_tablet .header_logo-block::before {
              display: block; } }
        .body .header .header_tablet .header_logo-block::after {
          content: '';
          height: 32px;
          width: 2px;
          border-radius: 1px;
          margin-right: 20px;
          background-color: #EAEAEA; }
        .body .header .header_tablet .header_logo-block .logo-block_logo {
          width: 98;
          height: 52;
          margin-right: auto; }
        .body .header .header_tablet .header_logo-block .logo-block_button {
          margin-right: 16px; }
        .body .header .header_tablet .header_logo-block .logo-block_button--call {
          display: none; }
          @media (min-width: 510px) {
            .body .header .header_tablet .header_logo-block .logo-block_button--call {
              display: block; } }
        .body .header .header_tablet .header_logo-block .logo-block_button--profile {
          display: none; }
          @media (min-width: 398px) {
            .body .header .header_tablet .header_logo-block .logo-block_button--profile {
              display: block; } }
        .body .header .header_tablet .header_logo-block .logo-block_button--chat {
          display: none; }
          @media (min-width: 454px) {
            .body .header .header_tablet .header_logo-block .logo-block_button--chat {
              display: block; } }
    .body .header .header_pc {
      display: none;
      background-color: #F8F8F8; }
      @media (min-width: 1366px) {
        .body .header .header_pc {
          display: flex;
          flex-wrap: wrap;
          flex-grow: 1;
          height: 88px;
          padding: 0 16px; } }
      .body .header .header_pc .header_pc-title {
        font-family: TTLakesBold;
        align-self: center;
        margin-right: auto; }
        .body .header .header_pc .header_pc-title::before {
          content: " ";
          position: absolute;
          left: 320px;
          width: 4px;
          height: 40px;
          background: #41F6D7;
          border-top-right-radius: 45px;
          border-bottom-right-radius: 45px; }
      .body .header .header_pc .header_pc-button {
        font-size: 16px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-family: TTLakesMedium;
        margin: 0%;
        padding: 0%;
        background: none;
        border: none; }
      .body .header .header_pc .header_pc-button--request::after {
        content: '';
        margin-right: 16px;
        margin-left: 8px;
        width: 40px;
        height: 40px;
        border: none;
        border-radius: 50%;
        background-color: #FF3E79;
        background-image: url(./static/repair.svg); }
      .body .header .header_pc .header_pc-button--etat::after {
        content: '';
        margin-left: 8px;
        width: 40px;
        height: 40px;
        border: none;
        border-radius: 50%;
        background-color: #FF3E79;
        background-image: url(./static/search.svg); }
  @media (min-width: 1366px) {
    .body .main {
      margin-left: 320px;
      min-width: 1046px; } }
  .body .footer {
    display: flex;
    flex-wrap: wrap;
    background: #F8F8F8;
    border-top: 1px solid #D9FFF5;
    align-items: center;
    min-width: 288px;
    align-self: flex-start;
    padding: 0 16px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;
    padding-bottom: 19px; }
    @media (min-width: 768px) {
      .body .footer {
        align-items: baseline;
        height: 105px;
        padding: 0 24px;
        display: flex;
        flex-direction: row;
        flex-grow: 1;
        justify-content: space-between;
        padding-bottom: 0; } }
    @media (min-width: 1120px) {
      .body .footer {
        min-width: 1020px;
        height: 85px;
        padding: 0 24px; } }
    @media (min-width: 1366px) {
      .body .footer {
        margin-left: 320px; } }
    .body .footer .footer_item {
      display: flex;
      flex-wrap: wrap;
      font-family: 'TTLakesRegular';
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      color: #7E7E82;
      margin-top: 16px; }
      .body .footer .footer_item:first-child {
        max-width: 218px; }
        @media (min-width: 768px) {
          .body .footer .footer_item:first-child {
            margin-top: 32px;
            text-align: left;
            width: 218px; } }
        @media (min-width: 768px) {
          .body .footer .footer_item:first-child {
            align-self: baseline;
            /*margin-top: 32px;*/ } }
      .body .footer .footer_item:last-child {
        max-width: 312px; }
        @media (min-width: 768px) {
          .body .footer .footer_item:last-child {
            text-align: right;
            width: 213px; } }
        @media (min-width: 830px) {
          .body .footer .footer_item:last-child {
            width: 330px; } }

button {
  border: none;
  background: none;
  margin: 0%;
  padding: 0%; }

.text--red {
