.swiper-pagination {
  position: unset; }
  @media (min-width: 768px) {
    .swiper-pagination {
      display: none; } }

.swiper-pagination-bullet {
  background: #DDDDDD; }

.swiper-pagination-bullet-active {
  background: #B5B6BC; }

