.nav-scroller {
  overflow-y: hidden;
  margin: 16px 0;
  display: flex;
  flex-wrap: wrap;
  padding-left: 16px; }
  @media (min-width: 768px) {
    .nav-scroller {
      padding-left: 24px; } }
  .nav-scroller .nav-scroller__items {
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
    width: 1365px; }
    @media (min-width: 1366px) {
      .nav-scroller .nav-scroller__items {
        flex-wrap: wrap;
        flex-grow: 1; } }
    .nav-scroller .nav-scroller__items .nav-scroller__item {
      color: #7E7E82;
      display: flex;
      padding: 0 8px;
      height: 40px;
      align-items: center;
      text-decoration: none;
      font-family: TTLakesMedium;
      margin-right: auto; }
      @media (min-width: 1366px) {
        .nav-scroller .nav-scroller__items .nav-scroller__item {
          margin-right: 0; } }
      @media (min-width: 1394px) {
        .nav-scroller .nav-scroller__items .nav-scroller__item {
          margin-right: auto; } }
    .nav-scroller .nav-scroller__items .nav-scroller__item--pc-link {
      display: none; }
      @media (min-width: 1366px) {
        .nav-scroller .nav-scroller__items .nav-scroller__item--pc-link {
          display: flex; } }
    .nav-scroller .nav-scroller__items .nav-scroller__item:nth-child(2) {
      background-color: #FFFFFF;
      border: 2px solid #B8FFEC;
      border-radius: 6px; }
    @media (min-width: 1366px) {
      .nav-scroller .nav-scroller__items .nav-scroller__item:first-child {
        background-color: #FFFFFF;
        border: 2px solid #B8FFEC;
        border-radius: 6px; } }
    @media (min-width: 1366px) {
      .nav-scroller .nav-scroller__items .nav-scroller__item:nth-child(2) {
        background: none;
        border: none; } }
