.section_prices {
  background-color: #F8F8F8;
  margin: 0;
  min-width: 320px;
  min-height: 234px;
  overflow: hidden; }
  @media (min-width: 1120px) {
    .section_prices {
      min-width: 1020px; } }

.prices {
  display: flex;
  flex-wrap: wrap;
  overflow: hidden;
  margin: 16px 16px 0 0;
  height: 238px;
  margin-right: 0; }
  @media (min-width: 768px) {
    .prices {
      height: unset;
      margin: 8px 24px 24px 24px;
      overflow: unset; } }
  .prices .prices_header {
    display: none; }
    @media (min-width: 768px) {
      .prices .prices_header {
        display: flex;
        flex-wrap: wrap;
        flex-grow: 1; } }
    .prices .prices_header .prices_header-item {
      font-family: 'TTLakesRegular';
      font-size: 14px;
      line-height: 16px;
      color: #7E7E82;
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      height: 64px;
      margin: 0 16px; }
      .prices .prices_header .prices_header-item .prices_header-element {
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
        align-self: center;
        width: 10%;
        flex-grow: 1; }
        .prices .prices_header .prices_header-item .prices_header-element:first-child {
          flex-grow: 12; }
  .prices .prices_offer {
    display: flex;
    height: 200px;
    border-radius: 6px;
    border-bottom: 16px;
    margin-left: 16px; }
    @media (min-width: 768px) {
      .prices .prices_offer {
        margin-left: 0;
