        flex-wrap: wrap;
        flex-grow: 1;
        height: 324px;
        background: #FFFFFF; } }
    .prices .prices_offer .prices_offer-item {
      position: relative;
      display: flex;
      flex-wrap: wrap;
      width: 260px;
      height: 200px;
      background: #FFFFFF;
      border-radius: 6px;
      margin-right: 16px; }
      @media (min-width: 768px) {
        .prices .prices_offer .prices_offer-item {
          flex-shrink: unset;
          width: 100%;
          height: 64px;
          margin: 0 16px;
          border-bottom: 1px solid #EAEAEA;
          border-radius: unset; } }
      .prices .prices_offer .prices_offer-item .prices_offer-element {
        display: flex;
        flex-wrap: wrap;
        font-family: 'TTLakesRegular';
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.2px;
        color: #1B1C21;
        width: 100%;
        margin-top: 16px;
        margin-left: 16px; }
        .prices .prices_offer .prices_offer-item .prices_offer-element:first-child {
          flex-grow: 12; }
        .prices .prices_offer .prices_offer-item .prices_offer-element:last-child {
          width: 119px;
          height: 32px;
          justify-content: end; }
        @media (min-width: 768px) {
          .prices .prices_offer .prices_offer-item .prices_offer-element {
            flex-grow: 1;
            width: 10%;
            align-self: center;
            margin-top: 0px;
            margin-left: 0px; } }

.prices_text {
  max-width: 288px;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0px 16px 16px 16px;
  font-family: 'TTLakesRegular';
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #1B1C21; }
  @media (min-width: 453px) {
    .prices_text {
      margin: 16px;
      max-width: 421px; } }
  @media (min-width: 768px) {
    .prices_text {
      margin-left: 24px; } }
  @media (min-width: 1120px) {
    .prices_text {
      margin-left: 32px; } }

.prices_button-offer {
  position: relative;
  padding: 0;
  margin-left: 16px;
  align-self: flex-start;
  border: none;
  background: none;
  font-family: 'TTLakesMedium';
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #1B1C21;
  margin-bottom: 40px;
  max-width: 192px;
  text-align: start; }
  .prices_button-offer::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 33px;
    left: 110px;
    height: 6px;
    width: 6px;
    border-top: 2px solid #FF3E79;
    border-right: 2px solid #FF3E79;
    border-top-left-radius: 1px;
    border-bottom-right-radius: 1px;
    transform: rotate(45deg); }
    @media (min-width: 453px) {
      .prices_button-offer::after {
        top: 9px;
        left: 297px; } }
  @media (min-width: 453px) {
    .prices_button-offer {
      max-width: 292px; } }
  @media (min-width: 768px) {
    .prices_button-offer {
      margin-left: 24px;
      margin-bottom: 47px;
      margin-bottom: 50px; } }

.prices_header-mobile {
  font-family: 'TTLakesRegular';
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82;
  width: 100%;
  margin-bottom: 4px; }
  @media (min-width: 768px) {
